export class BlogPost {
  _id: number;
  content: string;
  title: string;
  author: string;
  img: string[];

  //assign vals from json to properties
  constructor(id: number, content: string, title: string, image: string[]) {
    this._id = id;
    this.content = content;
    this.title = title;
    this.author = "Furkan";
    this.img = image;
  }
}
